import React, { useState } from "react";

const Contact = () => {

    const [data, setData] = useState({
        fullname: " ",
        phone: " ",
        email: " ",
        msg: " ",
    });

    const InputEvent = (event) => {
        const {name, value} = event.target;

        setData((preVal) => {
            return {
                ...preVal,
                [name] : value,
            };
        });
    };

    const formSubmit = (e) => {
        e.preventDefault();
    // console.log(`My Name is ${data.fullname} My Mobile No is ${data.phone} My mail is ${data.email} and My Message for you is ${data.msg}`);
    };
    return (
        <>
            <div className="my-5">
                <h1 className="text-center">
                    Contact Us
                </h1>
            </div>
            <div className="container contact_div ml-auto">
                <div className="row">
                    <div className="col-md-6 col-10 mx-auto">
                        <form onSubmit={formSubmit}>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Full Name</label>
                                <input type="text" class="form-control" id="exampleFormControlInput1"
                                    name="fullName" onChange={InputEvent} placeholder="Enter Your Name" />
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Phone No</label>
                                <input type="number" class="form-control" id="exampleFormControlInput1" onChange={InputEvent} placeholder="Enter Your Mobile No" />
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Email address</label>
                                <input type="email" class="form-control" id="exampleFormControlInput1" onChange={InputEvent} placeholder="name@example.com" />
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlTextarea1" class="form-label">Message</label>
                                <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" onChange={InputEvent} />
                            </div>
                            <div class="col-12 text-center mb-5">
                                <button class="btn btn-outline-primary text-center" type="submit">Submit</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>

        </>
    );
};

export default Contact;